#preloader {
  position: fixed;
  @include aic-jcc;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: $br_primary_4;
  z-index: 9;
  transition: 1s;
  pointer-events: none;

  &.close {
    opacity: 0;
  }
  svg {
    width: rem(160) !important;
  }
}